export default {
  name: "RightList",
  props: {
    // [{name:'xx',isSelect:true, children:[{name:'yy'},isSelect:true]}]
    list: {
      type: Array,
      default: () => [] //默认值是一个数组或者对象的，需要写成一个函数，返回值就是默认值

    }
  },
  methods: {
    handleClick(item) {
      if (!item.isSelect) {
        this.$emit("select", item);
      } // this.$emit("select",item);//抛出事件给父级,让父级去处理

    }

  }
};